.overlay {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay dialog {
  fill: #f3f3f3;
  border: none;
  outline: none;
  position: fixed;
  left: 20vw;
  top: 20vh;
  height: 60vh;
  width: 60vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.overlay dialog .itemName {
  margin-top: 3vh;
  color: #000;
  text-align: center;
  font-family: Inter;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}/*# sourceMappingURL=style.css.map */