.designLayout {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  .designSetting {
    fill: #f3f3f3;
    border: none;
    outline: none;
    position: fixed;
    left: 20vw;
    top: 20vh;
    height: 60vh;
    width: 60vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    .settingTitle {
      margin-top: 3vh;
      color: #000;
      text-align: center;
      font-family: Inter;
      font-size: 36px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
    .settingList {
      margin-top: 6vh;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      .setting {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 350px;
        height: 50px;
        border-radius: 10px;
        background: #d9d9d9;
        margin-bottom: 15px;
        -webkit-user-select: none;
        user-select: none;
        &:hover {
          transform: scale(1.2);
        }
      }
    }
  }
}
